.Deck {
  text-align: center;
}

.Deck-gimme {
  padding: 15px;
  background-color: black;
  color: white;
  border-radius: 8px;
  border: none;
  margin: 0 10px;
}

.Deck-gimme:hover {
  cursor: pointer;
  background-color: #444;
}

.Deck-gimme:active,
.Deck-gimme:focus {
  outline: none;
}

.Deck-cardarea {
  margin-top: 80px;
}

.Deck-title {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}